<template>
    <div  :class="bgColor" class="aorus-header">
      <div class="aorus-container">
        <div class="row">
          <div class="col-12">
            <span id="menuBtn" @click="menuFun()">
              <i class="fas fa-bars"></i>
            </span>
            
            <img class="logo" src="@/assets/images/logo.png">
            <ul :class="menuStatus?'active':''">
              <div class="menuHeader">
                <div class="closeMenuBtn"  @click="menuFun()">
                  <i class="fas fa-times"></i>
                </div>
              </div>
              <li class="menu-list">PRODUCTS <i class="fas fa-angle-down"></i></li>
              <li class="menu-list">BUILD YOUR OWN</li>
              <li class="menu-list">EXPLORE <i class="fas fa-angle-down"></i></li>
              <li class="menu-list">ESPORT</li>
              <li class="menu-list">SERVICE</li>
              <li class="menu-list">MEMBERSHIP</li>
            </ul>
            <span id="search-icon">
              <i class="fas fa-search"></i>
            </span>
            <span id="user-icon">
              <i class="fas fa-user" ></i>
            </span>
            <img class="GB-logo" src="@/assets/images/logo_gigabyte_white.png">
          </div>
        </div>
      </div>
    </div>
</template>

<script>
module.exports = {
  props: {
   bgColor: String
  },
  mounted:function(){
    var _this = this;
    this.initHeader();
    setInterval(function(){
      _this.initHeader();
    },10000)
  },
  watch:{
		
	},
  data(){
    return {
      menuStatus: false
    }
  },
  methods:{
    initHeader(){
      setTimeout(function(){
        var  herderList = document.getElementsByClassName('menu-list');
        for(var k=0 ; k<herderList.length; k++){
          herderList[k].classList.remove('showHeaderDown2');
          if(herderList[k].classList.contains('showHeader')==false){
            herderList[k].classList.add('showHeader');
          }
        }
        setTimeout(function(){
          var  herderList = document.getElementsByClassName('menu-list');
          for(var k=0 ; k<herderList.length; k++){
            if(herderList[k].classList.contains('showHeader')==true){
              herderList[k].classList.add('showHeader2');
              herderList[k].classList.remove('showHeader');
            }
          }
          setTimeout(function(){
            var  herderList = document.getElementsByClassName('menu-list');
            for(var k=0 ; k<herderList.length; k++){
              if(herderList[k].classList.contains('showHeader2')==true){
                herderList[k].classList.add('showHeaderDown');
                herderList[k].classList.remove('showHeader2');
              }
            }
            setTimeout(function(){
              var  herderList = document.getElementsByClassName('menu-list');
              for(var k=0 ; k<herderList.length; k++){
                if(herderList[k].classList.contains('showHeaderDown')==true){
                  herderList[k].classList.add('showHeaderDown2');
                  herderList[k].classList.remove('showHeaderDown');
                }
              }
            },1000);
          },700);
        },600);
      },300);
    },
    menuFun(){
      if(this.menuStatus){
        this.menuStatus = false
      }else{
        this.menuStatus = true
      }
    }

  }
}
</script>

